@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600&family=Roboto+Slab:wght@300;400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* For mobile phones: */
.app {

}

@media only screen and (min-width: 768px) {
  /* For desktop: */
  .app {
    background-color: #FBFBFB;
  }
}

.content {
  margin-left: 25%;
  margin-right: 1%;
}

.landing {

}

.title {

  margin-bottom: 20px;



  font-size: 130px;

  float: right;

  display: flex-inline;

}

.title .name {


  /* Quinn Voronin */
  width: 100%;

  padding-right: .1em;

  font-family: 'Inter', sans-serif;
  font-style: italic;
  font-weight: 600;
  font-size: auto;
  line-height: 1.2em;


  background-color: #191D32;
  color: #FBFBFB;


  /* identical to box height */
  float: right;
  text-align: center;
}

.title::after{
  display: inline;
}


.title .job {


  /* Inventor */
  margin-left: 7em;

  font-family: 'Inter', sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: .5em;
  line-height: 1em;

  background-color: #F3CA40;
  color: #101010;


  /* identical to box height */
  align-items: left;
  text-align: center;
}


.title .tagline {


  /* building solutions for the future */
  padding: .1em;

  font-family: 'Roboto Slab', serif;
  font-style: normal;
  font-weight: 600;
  font-size: .25em;
  line-height: 1em;

  color: #101010;


  /* identical to box height */
  align-items: center;
  text-align: right;
}


.title .seemore {


  background: #F3CA40;
  border: 5px solid #101010;
  box-sizing: border-box;
  border-radius: 77px;

  font-family: 'Roboto Slab', serif;
  font-style: normal;
  font-weight: 600;
  font-size: .2em;
  line-height: auto;

  float: right;
  align-items: center;
  text-align: center;
}

.landing img {

  float: right;

  width: 97%;
  height: auto;
}
.nav {
  display: inline;
  align-items: center;
  text-align: center;

  width: 100%;

}

.nav-open {
  -webkit-animation-name: navCollapse;
          animation-name: navCollapse;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  animation-direction: reverse;
  height: 25%;


  background: #191D32;

  font-size: 24px;

  display: flex;
}

.nav-closed {
  -webkit-animation-name: navCollapse;
          animation-name: navCollapse;
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-direction: normal;
          animation-direction: normal;

  height: 5%;

  background: #191D32;

  font-size: 24px;



}



@-webkit-keyframes navCollapse {
  0%    {height: 25%;}
  100%  {height: 5%; }
}



@keyframes navCollapse {
  0%    {height: 25%;}
  100%  {height: 5%; }
}

.nav ul {

  text-align: center;
  list-style-type: none;
  align-items: center;
  display: flex;

  width: 50%;

  float: right;

  margin: 0em;
}


.nav ul li {
  top: 50%;
  background-color: #FBFBFB;

  display: inline;

  border: none;
  border-radius: 2px;

  font-size: 1em;
  line-height: 1em;

  padding-top: .5em;
  padding-bottom: .5em;
  padding-left: .75em;
  padding-right: .75em;

  width: 15%;

  margin-left: .5em;
  margin-right: .5em;



}

.nav ul li a:link {
  text-decoration: none;
  color: #101010;

}
.nav ul li a:visited {
  text-decoration: none;
  color: #101010;

}
.nav ul li:hover {
  background-color: #d1d1d1;
}

.nav ul li a:hover {
}

.nav .wordmark a:link{
  color: #FBFBFB;
  display: inline;
  text-decoration: none;

  font-size: 1.5em;
  line-height: 1.5em;

  margin-top: .5em;
  margin-bottom: .5em;
  margin-left: 1em;
  margin-right: 1em;

  float: left;


}

.nav .wordmark a:visited{
  color: #FBFBFB;

}

.nav hr {
    border-top: 2px solid #101010;
    float: left;

}

.navLinks .links {
  width: 10%;

}

.media {
  width: 10%;
}

.contact-me {
  background: #F3CA40;

  float: right;

  border: none;
  border-radius: 2px;

  font-size: 1.5em;
  line-height: 1em;
  color: #101010;

  padding-top: .5em;
  padding-bottom: .5em;

  margin-bottom: .5em;
  margin-top: .5em;

  margin-left: 5%;
  margin-right: 5%;

  width: 10%;
}

.media img{
  width: 12.5%;
  height: auto;
  border-radius: 100px;
  background-color: #000;
  margin: .5em;

}

.bottom{
  position: absolute;
  bottom: 0;
  width: 100%;
}

